@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css");
@import url("reset.css");
@import url("navbar.css");
@import url("modal.css");
@import url("grid.css");
@import url("definitions.css");
@import url("montecarlo.css");

@font-face {
  font-family: Roboto-Regular;
  src: url("fonts/Roboto-Regular.ttf");
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/* General Purpose Classes */

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 88%;
}

/* HTML rules */

html,
body {
  height: 100%;
}

body {
  font-family: Roboto-Regular, "Trebuchet MS", Verdana, Helvetica, Arial, sans-serif !important;
  background-color: rgb(12, 20, 12);
}

#app > div {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

#center {
  position: relative;
  /* z-index: 0; /* #center excluded */
  flex: 1;

  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: stretch;
  justify-content: flex-start;
  align-content: stretch;
}

header {
  /* z-index: -1; /* header excluded */
  width: 100%;
  padding: 8px 0;

  margin: 0 auto;
  color: rgb(224, 248, 224);
  border-bottom: 1px solid black;
  height: 35px;
}

/*
#header-section {
  // z-index: 9999; // #header-section excluded //
}
*/

#header-section h3 {
  /* z-index: -10; /* #header-section was already excluded */
  display: flex;
  padding-left: 10px;
  font-size: 18px;
}

header ul.right-header-menu {
  position: absolute;
  /* z-index: 999; /* header ul.right-header-menu was already excluded */
  right: 0;
  top: 0;
  float: right;
  font-size: 13px;
  cursor: pointer;
}

header ul.right-header-menu li {
  float: left;
  padding: 5px;
  margin: 5px;
}

header i.header-icon {
  padding: 0 6px;
}

#refresh-fa i {
  color: yellow;
}

#reset-fa i {
  color: #ff0000;
}

.settings-fa i {
  color: #d3d3d3;
}

.settings-fa {
  margin-right: 8px;
}

.settings-fa.show-submenu {
  box-shadow: inset 0 0 2px 1px #c4c4c4;
  border-radius: 4px;
  background-color: #0c0c0c;
}

.settings-checkbox {
  float: right;
}

/* model */

#model-view {
  float: left;
  width: 75%;
  height: 100%;

  position: relative;
  padding: 1% 1%;

  overflow: auto;
  margin: 0 auto;
}

#bottom {
  /* z-index: 1; /* #bottom excluded */
  width: 100%;
}

/* Footer */

footer#control {
  display: block;
  clear: right;
}

footer#control button.CL-send-msg-button {
  float: left;
  width: 70px;
  box-shadow: 0 4px 4px -2px black;
  height: 24px;

  position: absolute;
  margin-top: 8px;
  right: 6px;
  height: 16px;
  width: 16px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  box-shadow: none;
}

.command-line-container {
  display: flex;
  flex-direction: row;
}

footer#control div.command-line-container div#prompt {
  flex: 0 1 auto;
  display: none;
  background-color: white;
}

footer#control div.command-line-container div#prompt p {
  color: #2f2fff;
  line-height: 34px;
  padding: 0 12px 0 6px;
  font-size: 16px;
  letter-spacing: 1px;
}

footer#control div.command-line-container div#prompt p i {
  padding-right: 8px;
}

footer#control input.in {
  float: left;

  flex: 1 1 auto;
  background-color: LightYellow;
  box-shadow: 0 4px 4px -2px black;
  font-size: 20px;
  letter-spacing: 0.5px;
  height: 34px;
  border: none;
  padding-left: 8px;
}

footer#control div.out {
  width: 70%;
  position: absolute;
  bottom: 62px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;

  height: 40px;
}

input#Response {
  width: 100%;
  height: 100%;
  background-color: rgba(168, 248, 168, 1);
  opacity: 1;
  border: none;
  padding-left: 10px;
  padding-right: 22px;
  border-radius: 26px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 1), 0 0 0 1px rgba(0, 0, 0, 1);

  font-size: 16px;
  text-align: center;
}

input#Response.error {
  background-color: rgba(224, 184, 128, 1);
}

input#Response[type="text"][disabled] {
  color: black;
}

.out {
  display: none;
}

.out.show {
  display: block;
}

#control .out .fa-times {
  position: absolute;
  /* z-index: 999; /* #control .out .fa-times excluded */
  right: 8px;
  top: 8px;
}

.copyright {
  width: 100%;
  clear: both;
  color: #a0f0a0;
  text-align: center;
  vertical-align: middle;
  padding: 6px 6px;
  font-size: 12px;
}

.box {
  border-radius: 5px;
  display: block;
  padding: 0.75rem 0 1.25rem 0.5rem;
}

/* text */

h1 {
  font-size: 160%;
  color: rgb(224, 248, 224);
}

h2 {
  font-size: 120%;
  color: rgb(224, 248, 224);
}

p {
  color: rgb(224, 248, 224);
}

.fa {
  padding-top: 0px;
  cursor: pointer;
}

/***************** Error messages ************************/

.messages-to-client {
  position: absolute;
  /* z-index: 1; /* .messages-to-client excluded */
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  background-color: red;
  color: white;
  height: 30px;
  line-height: 30px;
  font-size: 12px;
  font-weight: bold;
  transition: top 1s;
}

.messages-to-client-hidden {
  top: -65px;
  transition: top 1s;
}

.messages-to-client-displayed {
  top: 0;
  transition: top 1s;
}

/******************************************************************************/
/********************************* Tab panel **********************************/
/******************************************************************************/

.tabs {
  /* z-index: -1; /* .tabs excluded */
  float: left;
  margin-top: 0;
  max-height: 100%;
}

.tabs-labels {
  margin: 0;
  margin-top: 5px;
  padding-right: 32px;
}

.tabs-labels li {
  display: inline-block;
}

.tabs-labels li button {
  padding: 8px 12px;
  display: inline-block;
  color: rgb(160, 160, 0);
  background-color: rgb(16, 20, 16);
  text-decoration: none;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: default;
}

.tabs-labels li button:hover {
  color: yellow;
  background-color: rgb(128, 64, 64);
}

.tabs-labels li.active {
  border-bottom: 3px solid #bfff00;
  color: #bfff00;
  /* box-shadow: 0px -2px 1px 1px #BFFF00; */
}

.tabs-labels li.active a {
  color: #bfff00;
}

.tabs-labels li.active button {
  color: #bfff00;
}

input.editing-label {
  padding: 8px 12px;
  font-size: 15px;
  border: none;
}

.tabs-labels .fa-times {
  color: #77aa33;
  font-size: 12px;
  margin-right: 5px;
}

.tabs-labels .fa-times:hover {
  color: red;
}

.tabs-content {
  /* z-index: 0; /* .tabs-content excluded */
  /* padding-top: 25px; */
  height: 80vh;
  overflow-y: auto;
  border: 1px solid black;
}

.elem-wrapper {
  /* background: #363649; */
  /* background: #999999; */
  color: white;
  position: relative;
  border: none;
  box-shadow: 0 1px 4px 0 black;

  background-color: rgb(20, 24, 20);
  width: 100%;
  height: 100%;
  margin: 0 auto;
  /* min-height: 50px; */
  /* margin-bottom: 20px;*/
  overflow-x: auto;
  overflow-y: scroll;
}

.area-input {
  z-index: 0; /* .area-input */
  width: 100%;
  font-size: 18px;
  font-weight: normal;
  padding-left: 4px;
  height: 30px;
  color: #113311;
}

.elem-name {
  width: 100%;
  height: 28px;
  position: relative;
}

.elem-name .area-label {
  color: #ffad33;
  background-color: rgb(8, 12, 8);
  font-size: 18px;
  display: inline-block;
  min-width: 100%;
  padding-left: 4px;
  padding-top: 0px;
  padding-bottom: 0px;
  line-height: 32px;
}

.elem-wrapper .fa-bars {
  /* z-index: 1; /* .elem-wrapper .fa-bars excluded */
  display: block;
  float: right;
  right: 22px;
  top: 1px;
  margin: 0 auto;
  text-align: center;
  position: absolute;
  padding-top: 8px;
}

div.elem-value {
  /* padding-top: 32px; */
  /* if elem-name is hidden, padding-top: 4px */
  padding-left: 8px;
  padding-bottom: 4px;
  min-height: 25px;
}

div.editing input.edit {
  width: 100%;
  font-size: 16px;
}

.tab-section-mc-icon {
  display: inline-block;
  float: none;
  margin-left: 20px;
}

.react-grid-item > .react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  cursor: se-resize;
}

.react-grid-item > .react-resizable-handle::after {
  content: "";
  position: absolute;
  right: 3px;
  bottom: 3px;
  width: 5px;
  height: 5px;
  border-right: 2px solid rgba(0, 0, 0, 0.4);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
}

/******************************************************************************/
/********************************** Pop up ************************************/
/******************************************************************************/

/* investigate: this affects the area-menu, yet pop-up-menu is the area menu  */
div.popover-container {
  position: absolute;
  z-index: 1; /* div.popover-container (required to show popover above label) */
  right: 6px;
  margin-top: 10px;
}

div.popover-container div.popover {
  position: relative;
  margin-top: 5px;
}

div#model-section div.popover-container {
  right: auto;
  top: 28px;
  left: 230px;
}

.header-pop-up-menu {
  background-color: #971c17 !important; /* ? */
  width: 170px !important;
}

.pop-up-hidden {
  display: none;
}

/* pop-up-menu is the area menu */

.pop-up-menu {

  /* max-height: 634.656px; */
  visibility: visible;
  width: 150px;

  border: 1px solid transparent;
  /* box-shadow: 0 0 3px 0 white; */
  background-color: #c6c696;

  outline: none;
  padding-bottom: 5px;
}

.pop-up-menu-item {
  padding: 5px;
  color: black;
  font-size: 13px;
  height: 32px;
}

.pop-up-menu-item span {
  padding-left: 5px;
}

.pop-up-menu-item i {
  margin-right: 6px;
}

.pop-up-menu-item .checkbox {
  float: right;
}

/* Area menu */
.pop-up-menu-item:hover {
  color: black;
  padding: 5px;
  background-color: white;
  cursor: pointer;
  font-weight: bold;
}

/******************************************************************************/
/********************************* Droppable **********************************/
/******************************************************************************/

.Droppable {
  color: grey;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  top: 0;
  justify-content: center;
}

.tabs-content .Droppable {
  position: absolute;
  justify-content: left;
}

.dropzone-active p,
.dropzone-inactive p {
  text-align: center;
  vertical-align: middle;
  margin: auto;
  font-size: 70%;
  padding-top: 9px;
}

.dropzone-active p.drop-label,
.dropzone-inactive p.drop-label {
  padding-top: 50px;
}

.dropzone-inactive p {
  color: gray;
}

.Droppable .edit {
  display: flex;
  margin: 0 auto;
  margin-top: 9px;
}

.drop-child.hide {
  border: none;
}

.drop-child {
  border: 1px solid #202020;

  /*
  box-shadow: 1px 1px 0px #393939;
  border: 1px solid #393939;
  width: 32px;
  height: 32px;
  */
}

.drop-child:hover {
  color: white;
}

.fa-play-circle-o {
  color: green;
}

