/*******************************************************************/
/************************ react-grid-layout ************************/
/*******************************************************************/

.react-grid-layout {
  position: absolute;
  z-index: 0; /* .react-grid-layout */
  /* transition: height 200ms ease; */
}

.react-grid-item {
  /* z-index: -1; /* .react-grid-item excluded */
  transition: all 200ms ease;
  transition-property: left, top;
}

.react-grid-item.cssTransforms {
  transition-property: transform;
}

.react-grid-item.resizing {
  /* z-index: 1; /* .react-grid-item.resizing excluded */
  will-change: width, height;
}

.react-grid-item.react-draggable-dragging {
  transition: none;
  /* z-index: 3; /* .react-grid-item.react-draggable-dragging excluded */
  will-change: transform;
}

.react-grid-item.react-grid-placeholder {
  /* z-index: 2; /* .react-grid-item.react-grid-placeholder excluded */
  background: red;
  opacity: 0.2;
  transition-duration: 100ms;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.react-grid-item > .react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  background: url("data:image/svg+xml;base64,");
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
}

/*******************************************************************/
