/*

definitions.css
  
*/

#def-labels {
  display: block;
  float: left;
  width: 100%;
}

.modal-dialog-defs {
  position: relative;
  background-color: #e6e6e6 !important;
  border-radius: 2px !important;
  width: 550px !important;
}

#modal-header-fa {
  position: absolute;
  right: 5px;
  top: 2px;
}

.modal-body-defs {
  position: relative;
  height: 200px;
  padding-left: 0 !important;
}

.modal-body-defs div input {
  left: 0;
}

#def-box .item {
  position: relative;
  display: block;
  height: 25px;
}

#def-box,
.def-features {
  width: 92%;
  margin: 0 auto;
  position: relative;
  height: 100%;
}

#def-box h4 {
  text-align: center;
  color: #ffad33;
  font-size: 16px;
  letter-spacing: 1px;
}

#def-box p,
#def-box label {
  color: #000;
  font-size: 14px;
  display: inline-block;
  width: 120px;
  float: left;
  text-align: center;
  margin: 0 7px;
}

#lazy-label {
  width: 70px !important;
}

#def-box i {
  cursor: pointer;
}

#def-box .fa-ellipsis-v:hover {
  box-shadow: -3px -1px 2px #c4c4c4;
}

#def-box .addition {
  float: right;
  width: calc(100% - 87%);
  height: 3.5vh;
  color: #bfff00;
  padding: 3px 12px 0 10px;
  margin-left: 1px;
  margin-right: -7px;
  border-radius: 5px 0 0 5px;
  font-size: 18px;
}

#def-box input {
  margin-bottom: 5px;
}

#def-box input:not(.checkbox) {
  height: 3.5vh;
  border-radius: 3px;
  width: 120px;
}

#def-box input.checkbox {
  height: 3vh;
  border-radius: 3px;
  float: right;
  width: 16px;
}

.def-features {
  position: fixed;
  background: black;
  width: 200px;
  height: 220px;
  left: 174px;
  margin-top: -30px;
  /* z-index: 9999; /* .def-features excluded */
  box-shadow: 0 2px 5px white;
}

.def-features .fa {
  padding-top: 0;
  font-size: 18px;
}

.def-features .fa-table {
  height: 3.5vh;
  margin: 0 auto;
  color: #c4c4c4;
}

.def-features {
  padding: 0 10px;
}

.def-features label {
  display: block;
}

.def-features .fa-times {
  color: red;
}

.def-features .fa-check {
  color: green;
}

.def-features .fa-info-circle {
  color: yellow;
}

.def-features .size {
  display: block;
  padding-top: 5px;
}

.def-features .size label {
  display: block;
}

#def-box button {
  display: inline-block;
  margin: 5px 5px;
}

#def-box button .fa {
  display: block;
  width: 30px;
  height: 2.5vh;
}

.type-feature,
.value-feature {
  left: -174px;
  transition: left 1s;
  visibility: hidden;
}

.type-feature.showFeature,
.value-feature.showFeature {
  left: 174px;
  visibility: visible;
}

/*
.toolbox {
  // z-index: -1; // .toolbox excluded
  display: block;
}
*/

.addition.showFeature {
  box-shadow: -3px -1px 2px #c4c4c4;
}

.disabled-input {
  display: block;
  /* width: 400px!important; */
  border-radius: 0 !important;
}

.input-no-background {
  background: none;
  border: 0 none;
  border-bottom: 1px solid black !important;
}

.combo-wrapper > div {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 21.38px;
}

.combo-wrapper {
  display: inline-block;
  margin: 0 7px;
  margin-bottom: 5px;
}

.disabled-lazy-input {
  width: 40px !important;
  text-align: center;
  font-weight: bold;
  border-radius: 0 !important;
  font-size: 14px;
}

.feedback-input {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 500px !important;
  border-radius: 0 !important;
  background-color: #76bdd5;
  color: #000;
}

/******************************/
.definition-setup {
  display: flex;
  flex-direction: row;
  flex: 1 1 auto;
  min-width: 0;
  /*margin: 0 10px 5px 0;*/
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 20px;
  padding-bottom: 10px;
}

.lazy-label-definition {
  position: absolute;
  right: 50px;
  font-variant: normal;
  color: white;
  line-height: 30px;
  font-size: 14px;
}

.elem-wrapper #definition-pane {
  box-shadow: 0px 0px 6px 1px yellow; /* (?) */
}

.elem-wrapper.definition-wrapper {
  width: 50%; /* if react-grid-layout not activated (?) */
}

.definition-setup input {
  border: 0;
  border-bottom: 1px solid #e0e0e0;
  padding: 5px 0;
  font-size: 1em;
  font-weight: normal;
  background-color: transparent;

  color: #ffad33;
  font-size: 15px;
}

.definition-label {
  font-size: 14px;
  width: 70px;
  line-height: 30px;
}

.definition-setup label {
  color: white;
}
