/*

modal.css

Modal dialogs

*/

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  /* z-index: 999; /* .modal excluded */
  transition: opacity 1s ease-in;
  pointer-events: auto;
  overflow-y: auto;
}

.modal-dialog {
  position: relative;
  padding: 15px;
}

.modal-dialog i#close-modal-fa-icon {
  color: gray;
  position: absolute;
  top: 0;
  right: 0;
  margin: 10px;
  font-size: 18px;
}

.modal-dialog p {
  text-align: center;
  margin-top: 5px;
  margin-bottom: 5px;
  color: black;
}

.modal-dialog div.modal-body {
  margin: 12px auto;
  padding-left: 0;
  text-align: center;
}

.modal-dialog div.modal-body label {
  font-size: 14px;
  color: gray;

  display: block;
}

input#view-name {
  width: 256px;
  border-style: solid;
  border-color: #979797;
}

.modal-dialog div.modal-button {
  height: 24px;
}

.modal-dialog button {
  float: right;
  width: 50px;
  margin-right: 5px;
  margin-bottom: 5px;
}

/**/

button {
  background: #34495e;
  border: 0;
  color: #fff;
  min-width: 40px;
  border-radius: 4px;
  padding: 0;
  line-height: 32px;
}

div.setting-elem-container {
  display: flex;
  flex-direction: row;
}

div.setting-label {
  flex: 1 1 auto;
  line-height: 20px;
}

div.setting-value {
  flex: 1 1 auto;
  line-height: 20px;
}

.setting-value input.input-value {
  border: 0;
  border-bottom-color: currentcolor;
  border-bottom-style: none;
  border-bottom-width: 0px;
  border-bottom: 1px solid #e0e0e0;
  padding: 5px 0;
  font-size: 1em;
  font-weight: normal;
  background-color: transparent;
  color: black;
  font-size: 12px;
}
