/*

navbar.css

nav, definitions, and model side bars

*/

#left-hand-side {
  position: relative;
  float: left;
  width: 0px;
  margin-left: -160px;
  transition: margin-left 1s;
  /* z-index: 0; /* #left-hand-side excluded */
  margin-top: -30px;
}

div#model-section {
  background-color: rgb(12, 16, 12);
  min-width: 160px;
  max-width: 300px;
  float: left;
  flex: auto 0 0;
  align-self: stretch;
  top: 0;
  bottom: 0;
  box-shadow: 0 1px 4px 0 rgba(248, 248, 248, 1);
  /* z-index: 0; /* div#model-section excluded */
}

div#model-section .fa-sort {
  top: 12px;
  right: 8px;
  position: absolute;
}

.resizable-model-section {
  height: 85.6vh !important;
  min-width: 160px;
  max-width: 300px;
}

.tabs {
  margin-left: 0;
}

#left-hand-side.with-toolbox {
  width: 160px;
  margin-left: 0;
}

#left-hand-side.with-toolbox ~ .tabs {
  width: calc(100% - 185px); /* 174 + 25*/
}

#left-hand-side ~ .tabs {
  width: 98%;
}

#left-hand-side ul li a i.fa {
  float: right;
  color: white;
}

#left-hand-side li a,
#left-hand-side p {
  display: block;
  position: relative;
  padding: 5% 5%;
  text-decoration: none;
  /* vertical-align: middle; */
  line-height: 20px;
  letter-spacing: 1px;
  font-size: 78%;
  font-weight: normal;
}

#left-hand-side p {
  text-align: center;
  color: white;
  font-size: 14px;
  letter-spacing: 1px;
}

#left-hand-side li a {
  text-align: left;
  color: #d3d3d3;

  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

#model-section a:hover,
#model-section a:focus {
  cursor: pointer;
  color: #fff;
  background-color: black;
}

#model-section a:active {
  color: rgb(128, 64, 64);
}

/* Model list bar */

ul#model-list {
  overflow-y: auto;
  height: calc(100% - (110px + 34px + 24px)); /* *** */
}

ul#model-list div li,
div#model-section li.add-expression {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow-x: hidden;
  color: #cccccc;
  display: block;
  position: relative;
  padding: 7px 20px;
  text-decoration: none;
  /* vertical-align: middle; */
  line-height: 20px;
  letter-spacing: 1px;
  font-size: 78%;

  text-indent: 5px;
  font-size: 14px;

  clear: both;
}

ul#model-list div li:hover,
div#model-section li.add-expression:hover {
  cursor: move;
  background-color: rgb(128, 64, 64);
}

#tool-icons {
  position: relative;
  height: 35px;
  visibility: hidden; /* TEMP */
}

#tool-icons a {
  display: inline-block;
  height: 100%;
  width: 100%;
  color: gray;
  text-align: center;
  font-size: 14px;
  /* border-right: 1px solid #666666; */
  background-color: transparent;
  cursor: pointer;
  padding-top: 6px;
}

#tool-icons a:hover {
  color: white;
}

#tool-icons a {
  padding-top: 8px;
}

#tool-icons a.active {
  color: white;
  /* z-index: 9999; /* #tool-icons a.active excluded */
  margin-right: 2px;
}

#tool-icons a i.fa {
  font-size: 12px;
}

#tool-icons a span {
  font-size: 13px;
  padding-left: 8px;
}

#model-section div ul li div {
  padding: 0;
  margin: 0;
  width: 100%;
}

ul#model-list div.Droppable {
  display: block;
  height: 30px;

  position: absolute;
  bottom: 1px;
  left: 0;
  width: 100%;
}

.model-icons i {
  position: relative;
  height: 100%;

  color: #666666;
  text-align: center;
  padding: 4px 6px;
}

.model-icons i:hover {
  color: #fff;
}

ul.vertical-icon-menu {
  float: left;
  position: relative;
  /* z-index: 10; /* ul.vertical-icon-menu excluded */
  text-align: center;
  color: gray;
  height: 100%;
  background-color: black;
}

ul.vertical-icon-menu li {
  height: 50px;
  padding-top: 5px;
  padding-bottom: 9px;
  padding-left: 10px;
  padding-right: 10px;
}

ul.vertical-icon-menu li:hover {
  color: white;
  cursor: move;
}

ul.vertical-icon-menu li.active {
  background-color: #272c33;
  color: #ffad33;
}

ul.vertical-icon-menu li i.fa {
  display: block;
  clear: both;
  font-size: 17px;
}

ul.vertical-icon-menu li div {
  font-size: 11px;
  margin-top: 5px;
}

#definition-content {
  height: 100%;
  position: relative;
  background-color: #272c33;
  margin-left: 50px;
  box-shadow: 0 1px 4px 0 rgba(248, 248, 248, 1);
}

#object-palette,
ul.vertical-icon-menu {
  width: 60px;
}

#object-palette {
  flex: auto 0 0;
  align-self: stretch;
  top: 0;
  left: 0;
  bottom: 0;
  /* z-index: 0; /* #object-palette excluded */
}

#object-palette #definition-content {
  /* margin-left: -35vw; ??? */
  visibility: hidden;
  /* transition: margin-left 2s ease-out; ??? */
}

#object-palette.with-toolbox #definition-content {
  margin-left: 50px;
  visibility: visible;
  transition: margin-left 0.2s; /* ??? */
}

#object-palette.with-toolbox {
  width: 35vw;
}

.tabs {
  top: 0;
  bottom: 0;
  right: 0;
  margin-right: 0px !important;
  margin-left: 0px !important;
  left: 75vw;
  transition: left 1s; /* ??? */
  flex: 60 0 0;
  align-self: stretch;
  float: left;
}

#object-palette.with-toolbox ~ .tabs {
  width: 25vw; /* 174 + 25*/
  transition: width 0.5s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

.combo-wrapper {
  width: 85%;
  margin-left: 20px;
  margin-right: 4%;
}

.combo-wrapper > div {
  width: 100% !important;
}

