.commit-abort-section button {
  width: 40px;
  height: 30px;
  border-radius: 5px;
  font-size: 20px;
}

.commit-abort-section button:hover {
  cursor: pointer;
}

.commit-abort-section button i {
  padding-top: 0;
}

.commit-abort-section button:first-child {
  margin-right: 10px;
  color: green;
}

.commit-abort-section button:last-child {
