}

.header-label {
  height: 50px;
  line-height: 50px;
  margin: 0 auto;
  font-size: 16px;
  width: 100%;
  text-align: center;
  letter-spacing: 1px;
  font-variant: small-caps;
  color: #ffad33;
}

.header-label-options {
  position: absolute;
  right: 10px;
  top: 5px;
}

#model-section .header-label-options {
  right: 20px;
}

.add-first-view-message {
  text-align: center;
  color: #d1d1d1;
  letter-spacing: 1px;
  font-size: 14px;
}

.tabs-labels,
.tabs-content {
  margin-left: 20px !important;
  margin-right: 20px !important;
}

/**/

.tabs-shrinked-object-palette-not-model {
  left: calc(50px + 50vw);
  transition: left 1s;
}

.tabs-shrinked-model-not-object-palette {
  left: calc(35vw + 10vw);
  transition: left 1s;
}

.tabs-shrinked-object-palette-and-model {
  left: calc(50px + 10vw);
  transition: left 1s;
}

#object-palette.and-model ~ .tabs {
  width: calc(100vw - 50vw - 80px);
  transition: width 1s;
}

/*
#object-palette.not-model ~ .tabs {
  max-width: calc(100vw - 10vw - 70px);
  width: calc(100vw - 10vw - 70px);
  transition: width 1s;
}
*/

#model-section.expanded {
  width: 50vw;
}

#model-section.not-object-palette {
  left: 50px;
}

#model-section.and-object-palette {
  left: 35vw;
}

#model-section.and-object-palette:not(.expanded) ~ .tabs {
  /* shrinked model section */
  width: calc(100vw - 35vw - 10vw);
}

.definition-disabled-input {
  background-color: #c4c4c4 !important;
  flex-grow: 1;
}

.vertical-icon-menu li svg {
  height: 20px;
  display: block;
  margin: 0 auto;
}

.svg-icon-object-palette {
  fill: gray;
}

ul.vertical-icon-menu li:hover svg g.svg-icon-object-palette {
  fill: white;
}

ul.vertical-icon-menu li.active svg g.svg-icon-object-palette {
  fill: #ffad33;
}

ul.vertical-icon-menu li:hover #number-svg .st2 {
  stroke: white;
}

ul.vertical-icon-menu li.active #number-svg .st2 {
  stroke: #ffad33;
}

ul.vertical-icon-menu li:hover #simple-variable .st0 {
  fill: white;
}

ul.vertical-icon-menu li.active #simple-variable .st0 {
  fill: #ffad33;
}

ul.vertical-icon-menu li:hover #array .st1 {
  stroke: white;
}

ul.vertical-icon-menu li:hover #array .st2 {
  fill: white;
}

ul.vertical-icon-menu li.active #array .st1 {
  stroke: #ffad33;
}

ul.vertical-icon-menu li.active #array .st2 {
  fill: #ffad33;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 22px;
}

/* Hide default HTML checkbox */
.switch input {
  display: none;
}

/* The slider */
.slider {
  width: 50px;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 80px;
  /* left: 320px; */
  /* right: -120px; */
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 15px;
  width: 15px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #69ea92;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/* svg integer icon */
#number-svg .st0 {
  fill: url(#SVGID_1_);
}

#number-svg .st1 {
  fill: #120015;
}

#number-svg .st2 {
  fill: none;
  stroke: gray;
  stroke-width: 8;
  stroke-miterlimit: 10;
}

#number-svg .st3 {
  fill: none;
}

/* svg array icon */
#array .st0 {
  fill: none;
  stroke: #000000;
  stroke-width: 8;
  stroke-miterlimit: 10;
}

#array .st1 {
  fill: none;
  stroke: gray;
  stroke-width: 14;
  stroke-miterlimit: 10;
}

#array .st2 {
  fill: gray;
}

#simple-variable .st0 {
  fill: gray;
  stroke: #000000;
  stroke-width: 10;
  stroke-miterlimit: 10;
}

svg#mathSymbols {
  margin: 5px 0;
  margin-left: 5px;
  margin-right: 5px;
  /* float: left; */
}

svg#mathSymbols:hover {
  cursor: move;
}
