/*

grid.css

Tab content elements

*/

table {
  width: 100%;
}

tr:nth-child(odd) {
  background-color: rgb(24, 48, 24);
}

tr:nth-child(even) {
  background-color: rgb(16, 24, 16);
}

table th,
table td {
  /* width: 20%; */
  padding: 1px;
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
}

.grid-rv {
  position: relative;
  margin: 0 auto;
  background-color: transparent;
  z-index: auto; /* .grid-rv */
}

div.grid-cells {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: visible;

  background-color: #0c0e0c;
  color: rgba(248, 216, 128, 1);
  border: 0.5px solid rgba(128, 164, 2, 0.33);
  padding-top: 0;
}

.grid-cells .fa-cog {
  float: left;
}

div.grid-cells:focus {
  /* box-shadow: 0px 0px 10px green; */
  border: 2px groove #1c9b1c;
  /* z-index: 1; /* div.grid-cells:focus excluded */
  overflow: hidden;
}

div.grid-cells.focused-cell {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 6px #7ab5d3;
  outline: green auto 1px;
}

input.grid-cells {
  /*background-color: #000f00;*/
  background-color: #0c0e0c;
  color: white;
  border: 1px solid gray;
  /* transition: width 1s; */ /*, top 1s*/
}

.ReactVirtualized__Table {
  /*background-color: #c0cec0;*/
  /*width: 750px;*/
  margin: auto 20px;
}

.ReactVirtualized__Table__headerRow {
  background-color: rgb(20, 24, 20);
}

.ReactVirtualized__Table__headerRow div {
  color: #ffad33;
  text-align: center;
  font-weight: normal;
}

.ReactVirtualized__Table__row {
  background-color: #d9d9d9;
}

.ReactVirtualized__Table__row div {
  color: black;
}

/* Virtualized grid */

.ReactVirtualized__Grid {
  outline: none;
}

.ReactVirtualized__Grid input {
  padding-left: 5px;
  /*border: 1px solid black;*/
  position: absolute;
  font-size: 18px !important;
  box-shadow: 0px 0px 5px 1px white;
  left: 0;
}

/*********************************************************************************/

.Row {
  width: 100%;
  height: 100%;
  border-bottom: 1px solid black;
  display: flex;
  align-items: center;
  padding: 0 0.5rem;
  user-select: none;

  /* Draggable rows should be above non-draggable List */
  /* z-index: 2; /* .Row excluded */
}

.DraggableHeader {
  position: relative;
  padding-right: 1rem;
}

.DragHandle {
  position: absolute;
  right: 0;
  top: 0;
  /* z-index: 3; /* .DragHandle excluded */
  cursor: col-resize;
  color: #ccc;
  /* padding: 0 0.5rem; */
}

.DragHandle:hover,
.DragHandleActive {
  color: #f6a;
}

.LeftSideGridContainer {
  flex: 0 0 75px;
  /* z-index: 10; /* .LeftSideGridContainer excluded */
}

.LeftSideGrid {
  overflow: hidden !important;
}

.HeaderGrid {
  width: 100%;
  overflow: hidden !important;
}

.LeftSideGridContainer .ReactVirtualized__Grid__innerScrollContainer div {
  text-align: center;
  /*padding-right: 5px;*/
  padding: 7.5px 6px;
}

.LeftSideGridContainer div,
.headerCell {
  color: #ffad33;
}

.cell,
.headerCell,
.leftCell {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 0.5em;
}

.headerCell,
.leftCell {
  font-weight: bold;
}

.lazy-row {
  width: 100% !important;
  z-index: 1 !important; /* .lazy-row: places formula above generated cells */
  left: 0 !important;
}

.lazy-array-edit {
  background-color: #fff !important;
  width: 100% !important;
  color: black !important;
  /*top: 0!important;*/
  left: 0 !important;
  /*transition: top 1s;*/
}

.lazy-array-row {
  background-color: rgba(0, 0, 0, 0.1) !important;
  color: #666666 !important;
  border: 1px solid rgba(0, 0, 0, 0.1) !important;
  visibility: hidden;
}

.right-grid.lazyEdit .ReactVirtualized__Grid {
  width: 95% !important;
}

.right-grid.lazyEdit
  .ReactVirtualized__Grid
  .ReactVirtualized__Grid__innerScrollContainer {
  max-width: none !important;
  width: 100% !important;
}

.LeftSideGridContainer.lazyEdit {
  visibility: hidden;
}

.grid-rv.lazyEdit {
  width: 100% !important;
  transition: width 1s;
}

.child-grid-lazy {
  overflow-y: hidden !important;
  max-width: 100% !important;
  width: 100% !important;
}

.fa-expand {
  position: absolute;
  right: 6px;
  bottom: 6px;
}

.fa-expand:hover {
  color: #bfff00;
}

.grid-cells input {
  height: 100%;
  width: 100%;
  top: 0;
}

.focus-div {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 6px #7ab5d3;
  /*outline: #D7D7D7 auto 1px;*/
  /* z-index: 1; /* .focus-div  excluded */
}

.grid-cell-right {
  text-align: right;
}

.child-grid-lazy .ReactVirtualized__Grid__innerScrollContainer {
  width: 95% !important;
  max-width: 98% !important;
}

.grid-cells.lazy-array-edit.focus-div.grid-cell-right input {
  font-size: 15px;
  letter-spacing: 0.5px;
}

/***********************************************...*/

/* Table default theme */

.ReactVirtualized__Table__headerRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: black;
}

.ReactVirtualized__Table__row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.ReactVirtualized__Table__headerTruncatedText {
  display: inline-block;
  max-width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.ReactVirtualized__Table__headerColumn,
.ReactVirtualized__Table__rowColumn {
  margin-right: 10px;
  min-width: 0px;
}

.ReactVirtualized__Table__rowColumn {
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ReactVirtualized__Table__headerColumn:first-of-type,
.ReactVirtualized__Table__rowColumn:first-of-type {
  margin-left: 10px;
}

/*//////////////////////////////////////*/

.ReactVirtualized__Table__headerRow,
.ReactVirtualized__Table__row {
  border-bottom: 1px solid #000;
  border-left: 1px solid #000;
  border-right: 1px solid #000;
}

.ReactVirtualized__Table__row {
  background-color: rgb(20, 24, 20);
}

.ReactVirtualized__Table__headerRow {
  /* border-top: 1px solid #e0e0e0; */
  border-top: 1px solid #000;
  padding-top: 15px;
  padding-bottom: 15px;
}

.ReactVirtualized__Table__rowColumn {
  color: #e6e6e6 !important;
  padding-top: 3px;
  padding-bottom: 3px;
}

#model-table
  .ReactVirtualized__Grid__innerScrollContainer
  > div:nth-child(even) {
  background-color: transparent;
}

.full-grid-width {
  width: 100% !important;
  background-color: rgb(16, 24, 16) !important;
}

.full-grid-width:hover {
  background-color: #383d38 !important;
}

.left-grid {
  float: left;
}

.grid-cells {
  height: 100%;
}

.react-contextmenu-wrapper {
  height: 100%;
}

.react-contextmenu {
  position: absolute !important;
  top: 28px !important;
  right: 10px !important;
  left: auto !important;

  min-width: 120px;
  padding: 5px;
  font-size: 16px;
  color: #373737;
  text-align: left;
  background-color: rgb(248, 248, 248);
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
  outline: none;
  opacity: 0;
  pointer-events: none;
  transition: opacity 250ms ease !important;
  z-index: auto; /* .react-contextmenu */
}

.react-contextmenu-item {
  cursor: pointer;
}

.react-contextmenu div {
  padding: 5px;
}

.delete-section {
  margin-top: 5px;
  border-top: 1px dashed gray;
}

.elem-value .modal .modal-dialog {
  position: fixed !important;
  top: 0;
  left: 0;
  right: 0;
}
