montecarlo.css

*/

.MC-setup-wrapper {
  padding: 6px 0;
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: flex-end;

  width: 95%;
  margin: 0 auto;
  margin-bottom: 10px;
}

.MC-setup {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  min-width: 0;
  margin: 0 12px 6px 0;
}

.MC-setup label {
  font-size: 14px;
  color: #80c080;
}

.MC-setup input {
  border: 0px;
  border-bottom: 1px solid #80b080;
  padding: 4px 0;
  font-size: 1em;
  font-weight: normal;
  background-color: transparent;

  color: #ffad33;
  font-size: 15px;
}

.MC-setup-wrapper button {
  height: 36px;
  margin-bottom: 5px;
}

svg.chart {
  overflow-x: auto;
}

svg.chart g text {
  fill: #fff;
}

.histogram {
  overflow: auto;
  overflow-y: hidden;
}

.mc-button-disabled {
  background-color: gray;
}
